<template>
  <div class="vchart">
    <m-chart :option="option" :mode="this.mode" />
  </div>
</template>

<script>
export default {
  props: {
    xData: {
      type: Array,
    },
    yData: {
      type: Array,
    },
    title: {
      type: String,
      default: '柱状图'
    },
    mode: {
      type: String,
      default: "dark",
    },
  },
  created() {
    this.draw();
  },
  watch: {
    xData: function () {
      this.draw();
    },
    yData: function () {
      this.draw();
    },
    title: function () {
      this.draw();
    },
  },
  data() {
    return {
      option: null,
    };
  },
  methods: {
    draw() {
      let mainColor = "#B0C4DE";
      let lineColor = "#ffff00";

      if (this.mode === undefined) {
        // do nothing
      } else if (this.mode === "light") {
        mainColor = "black";
        lineColor = "#208aec";
      } else {
        // do nothing
      }
      this.option = {
        title: {
          text: this.title,
          textStyle: {
            color: mainColor,
          },
        },
        grid: {
          bottom: "15%",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          textStyle: {
            color: mainColor,
          },
        },
        xAxis: {
          type: "category",
          data: this.xData,
          scale: true,
          splitLine: {
            show: false,
          },
          textStyle: {
            color: mainColor,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: false,
          },
          scale: true,
          textStyle: {
            color: mainColor,
          },
        },
        series: {
          data: this.yData,
          type: "bar",
          markLine: {
            data: [
              {
                name: "average",
                type: "average",
              },
            ],
          },
          label: {
            normal: {
              show: true,
              position: "top",
              textStyle: {
                color: mainColor,
              },
            },
          },
          itemStyle: {
            normal: {
              color: lineColor,
            },
          },
        },
        textStyle: {
          color: mainColor,
        },
      };
    },
  },
};
</script>

<style scoped>
.vchart {
  height: 100%;
  width: 100%;
  color: lightgray;
}
</style>>

