import Vue from 'vue'
import App from './App.vue'
import store from './store'
import ECharts from 'vue-echarts' // refers to components/ECharts.vue in webpack
import axios from 'axios'
import VueAxios from 'vue-axios'
import vuescroll from 'vuescroll';
import router from './router'
import theme from "./echarts/theme.json";
import ElementUI from 'element-ui'
import qs from 'qs'
import VueSession from 'vue-session'

import XLSX from 'xlsx'
import { ResizeObserver } from 'vue-resize';
import 'vue-resize/dist/vue-resize.css';

import LineEcharts from './echarts/lineEcharts.vue'
import EchartsData from './echarts/echartsData.vue'
import EchartsFull from "./echarts/echartsFull.vue";
import MyEcharts from './echarts/myEcharts.vue'
import BarEcharts from './echarts/barEcharts.vue'

Vue.component('resize-observer', ResizeObserver);

Vue.component('m-chart',MyEcharts)
Vue.component('line-chart',LineEcharts)
Vue.component('echarts-data',EchartsData)
Vue.component('echarts-full', EchartsFull)
Vue.component('bar-chart',BarEcharts)

import 'element-ui/lib/theme-chalk/index.css'
import '@babel/polyfill'
import 'mutationobserver-shim'

import 'vuescroll/dist/vuescroll.css';
import './plugins/bootstrap-vue'

import "echarts/lib/chart/line";
import "echarts/lib/chart/bar";
import "echarts/lib/chart/pie";
import "echarts/lib/chart/gauge"
import "echarts/lib/chart/boxplot"
import "echarts/lib/chart/scatter"

import "echarts/lib/component/dataZoom";
import "echarts/lib/component/markLine";
import "echarts/lib/component/markPoint";
import "echarts/lib/component/legend";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/grid";
import "echarts/lib/component/title";
import "echarts/lib/component/graphic";
import "echarts/lib/component/visualMap";
import "echarts/lib/component/markArea";
import "echarts/lib/component/legendScroll";

Vue.prototype.$qs = qs

Vue.use(VueAxios, axios)
Vue.use(require('vue-moment'));
Vue.use(vuescroll);
Vue.component('v-chart', ECharts)
ECharts.registerTheme("mytheme", theme);
Vue.use(ElementUI)
Vue.use(qs)
Vue.use(VueSession)
Vue.use(XLSX)

Vue.prototype.echartsResize = function(ref){
  setInterval(() => {
    window.onresize = function () {
      ref.resize()
    }
  }, 100)
}

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
